.mk_select {
  height: 1.6rem;
}
.mk_select > span {
  display: none !important;
}
.mk_select_icon {
  width: 1.2rem;
}
.mk_select_icon .mk_icon {
  transition: all 0.5s;
  fill: #b4b3b3 !important;
}
.mk_select__control {
  min-height: initial !important;
  height: 100% !important;
  box-shadow: inherit !important;
  border-radius: 0rem !important;
  border: 0.05rem solid #e8e8e8 !important;
  transition: all 0.5s !important;
}
.mk_select__control--is-focused {
  border-color: #0693e3 !important;
  box-sizing: border-box !important;
  border-width: 0.05rem !important;
}
.mk_select__control--is-focused .mk_select_icon .mk_icon {
  fill: #7c7c7c !important;
}
.mk_select__control:hover {
  border-color: #0693e3 !important;
}
.mk_select__control:hover .mk_select_icon .mk_icon {
  fill: #7c7c7c !important;
}
.mk_select__placeholder {
  white-space: nowrap;
}
.mk_select__value-container {
  min-width: 3.5rem;
}
.mk_select__indicator {
  padding: 0 0.4rem !important;
}
.mk_select__indicator-separator {
  display: none;
}
.mk_select__menu {
  border-radius: 0rem !important;
}
.mk_select__option:hover {
  background: rgba(6, 147, 227, 0.65) !important;
}
.mk_select__option--is-selected {
  background: #0693e3 !important;
}
.mk_select__option--is-focused {
  background: rgba(6, 147, 227, 0.8) !important;
}
.mk_select__desc {
  line-height: 1.6rem;
}
